<template>
	<view class="" style="display: flex;flex-flow: column nowrap;align-items: center;width: 750upx; ">
		<navigationview :current="current" :values="items" v-on:clickItem="onClickItem" :list_id="list_id"></navigationview>


		<view class="jichukuangjia" :style="'height:'+height+'px'  " v-show="current === 0">
			<mpvue-echarts :echarts="echarts" :onInit="pieInit" canvasId="pie" />
			<view v-if="department_name" class="title_view" @click="goto_item">
				{{department_name}}
			</view>

		</view>

		<scroll-view scroll-y class="jichukuangjia" :style="'height:'+height+'px' " v-if="current === 1">
			<handleview :card_id="luyouData[list_id].card_id"></handleview>
		</scroll-view>

	</view>
</template>

<script>
	import navigationview from '../../../components/navigation.vue';

	import * as echarts from '../../../components/echarts/echarts.tree.min.js';
	import mpvueEcharts from '../../../components/mpvue-echarts/src/echarts.vue';
	import handleview from '../../../components/handle-view.vue';
	var pieChart = null;

	export default {


		data() {
			return {
				luyouData: [{}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {},
					{}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}, {},
				],
				list_id: null,
				x: -200,
				y: -200,
				items: [
					'待商榷的现状',
					'我的建议',
				],
				department_name: "",
				department_id: "",
				height: 0,
				current: 0,
				item_card_department_tree: null,
				item_card_department_list: null,
				echarts,
			}
		},
		components: {
			handleview,
			navigationview,
			mpvueEcharts
		},
		onLoad: async function(option) {
			var that = this;
			this.height = uni.getSystemInfoSync().windowHeight - 70 * (uni.getSystemInfoSync().windowWidth / 750);
			console.log("list_id:" + option.list_id)
			this.list_id = option.list_id;
			setTimeout(() => this.deal_with_data(), 500);
		},


		methods: {
			onClickItem(index) {
				if (this.current !== index) {
					this.current = index;
				}
			},
			pieInit: function(canvas, width, height, data) {
				var that = this;
				console.log(echarts);
				pieChart = echarts.init(canvas, null, {
					width: width,
					height: height
				})
				canvas.setChart(pieChart)
				return pieChart
			},



			deal_with_data: async function() {

				const that = this;
				let getdataresult0 = await that.get_hot_card_list();
				let getdataresult1 = await that.getData_tree();
				let getdataresult2 = await that.getData_list();

				pieChart.setOption(this.chose_option());
				pieChart.on('click', function(param) {
					console.log("点击事件:", param.data);
					that.department_name = param.data.name;
					that.department_id = param.data.value;
				})
			},


			fangda() {
				this.fangdabeishu += 0.3;
				pieChart.setOption(this.chose_option())

			},
			suoxiao() {
				this.fangdabeishu -= 0.3;
				pieChart.setOption(this.chose_option())
			},
			chose_option() {
				var option = this[this.luyouData[this.list_id].getechartfunction](this.item_card_department_tree)
				return option
			},



			goto_item: function() {
				uni.navigateTo({
					url: "../item_detail/item_detail?data=" + JSON.stringify(this.item_card_department_list[this.department_id]) +
						"&card_id=" + this.luyouData[this.list_id].card_id,
				})
			},



			getData_tree() {
				return new Promise((res) => {
					uni.request({
						url: 'https://kaikaiomg.applinzi.com/uni_app_download.php',
						method: "POST",
						data: {
							'form': this.luyouData[this.list_id].getdata_tree_title,
						},
						header: {
							'content-type': 'application/x-www-form-urlencoded'
						},
						fail: (e) => {
							res(e);
						},
						success: (ret) => {
							if (ret.statusCode !== 200) {
								console.log("请求失败:", ret)
							} else {
								console.log("下载数据树图:", ret.data)
								// let lists = [];
								this.item_card_department_tree = ret.data.data;
								// console.log("lists:", ret.data.data.length)
								// console.log("lists:", ret.data.data)
							}
							res(ret);
						}
					});
				})
			},
			getData_list() {
				return new Promise((res) => {
					uni.request({
						url: 'https://kaikaiomg.applinzi.com/uni_app_download.php',
						method: "POST",
						data: {
							'form': this.luyouData[this.list_id].getdata_title,
						},
						header: {
							'content-type': 'application/x-www-form-urlencoded'
						},
						fail: (e) => {
							res(e);
						},
						success: (ret) => {
							if (ret.statusCode !== 200) {
								console.log("请求失败:", ret)
							} else {
								console.log("下载数据:", ret.data)
								// let lists = [];
								this.item_card_department_list = ret.data.data;
								// console.log("lists:", ret.data.data.length)
								// console.log("lists:", ret.data.data)
							}
							res(ret);
						}
					});
				})
			},


			guanli_gov_stru(data) {
				return {
					tooltip: {
						trigger: 'item',
						triggerOn: 'mousemove'
					},
					series: [{
						type: 'tree',

						data: [data],

						left: '2%',
						right: '2%',
						top: '8%',
						bottom: '20%',

						symbol: 'emptyCircle',
						symbolSize: 17,

						orient: 'horizontal',

						expandAndCollapse: true,

						label: {
							normal: {
								position: 'top',
								// rotate: -90,
								verticalAlign: 'middle',
								align: 'right',
								fontSize: 9
							}
						},
						leaves: {
							label: {
								normal: {
									position: 'right',
									verticalAlign: 'middle',
									align: 'left',
								}
							}
						},
						// roam:'move',
						roam: true,

						animationDurationUpdate: 750,
					}]

				}
			}


		}
	}
</script>

<style>
	.jichukuangjia {
		display: flex;
		/*   position: fixed;
        bottom: 0upx; */
		/* 		background-image: url('http://kaikaiomg-kaikaiomg.stor.sinaapp.com/uni_app_image/bc.jpg'); */
		background-color: white;
		width: 750upx;
		overflow: hidden;
		flex-flow: column nowrap;
		justify-content: center;
		align-items: center;
	}

	.title_view {
		position: absolute;
		padding: 10upx;
		background-color: #1482D1;
		border-radius: 10upx;
		top: 100upx;
		left: 20upx;
		font-size: 20upx;
		color: white;
		z-index: 99;
	}
</style>



